	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Closed For Ever?","date":"19 Sep 2004","slug":"/2004-09-19-closed-for-ever/","tags":"Y2004"},"html":"<p>Is <a href=\"http://www.metafilter.com/newuser.mefi\">MetaFilter permanently closed to new users?</a> I've been trying off and on for several months to sign up, and keep getting told that signups are closed.</p>\n<p>I was reminded of this when I saw <a href=\"http://www.paradox1x.org/weblog/kmartino/archives/003461.shtml\" title=\"paradox1x: Johnny Ramone, Rest in peace\">Karl's post on the death of Johnny Ramone</a>.</p>"},"next":{"fields":{"title":"Brown Nosing","date":"19 Sep 2004","slug":"/2004-09-19-brown-nosing/"}},"previous":{"fields":{"title":"End of Ladenschluss As We Know It","date":"19 Sep 2004","slug":"/2004-09-19-end-of-ladenschluss-as-we-know-it/"}}},{"node":{"fields":{"title":"Brown Nosing","date":"19 Sep 2004","slug":"/2004-09-19-brown-nosing/","tags":"Y2004"},"html":"<p>State elections today in Brandenburg and Sachsen, and the media at home and abroad is bemoaning that the <a href=\"http://news.bbc.co.uk/2/hi/europe/3669974.stm\" title=\"BBC NEWS | Europe\">German far right makes poll gains</a>. While the results of the far-right (9% in Sachsen, 6% in Brandenburg) make nice headlines, they miss the point. The real story is the failure of both main parties to maintain their ground. In both Sachsen (CDU 41.9, SPD 9.5) and Brandenburg (SPD 32.1, CDU 19.4), the two <em>Volksparteien</em> combined for barely over 50%. Neither Schröder nor Merkel/Stoiber/Koch have the confidence of voters in the east.</p>\n<p>The big winners were the post-Communist PDS, finishing strong second in both contests. Does this foreshadow a return of the PDS to the Bundestag in 2006, tipping the result to deny victory to both SPD and Union?</p>\n<p>As for the far right, they've always been utterly incompetent at parliamentary work, so expect the factions to self-destruct sooner than later. The worst thing is that this result entitles the fringe parties to increased state financing, which is based on voter counts. Not that the main parties would ever agree to reform their finances and eliminate such state subsidies. They themselves are too dependent on them.</p>"},"next":{"fields":{"title":"Back In Germany","date":"18 Sep 2004","slug":"/2004-09-18-back-in-germany/"}},"previous":{"fields":{"title":"Closed For Ever?","date":"19 Sep 2004","slug":"/2004-09-19-closed-for-ever/"}}},{"node":{"fields":{"title":"Back In Germany","date":"18 Sep 2004","slug":"/2004-09-18-back-in-germany/","tags":"Y2004"},"html":"<p>We've returned from our getaway on Tegernsee, which seems to be the Munich equivalent of the Alster, with the same high real estate prices, except that it's outside the city, not right in it. Very poor traffic planning, there were always traffic jams. Lots of private clinics though. It must be the dream of every German doctor to open a private clinic on Tegernsee.</p>\n<p>This is the first time I've spent any time in Bavaria (outside of Munich, which according to the Munichers I know is not part of Bavaria at all, despite being the Bavarian capital). Maybe it's just the north German prejudices that I've picked up, but it didn't feel very German at all. It felt more like Austria.</p>\n<p>I'll quit now that I've insulted all of the German-speaking regions of Europe (including the Swiss, by not mentioning them).</p>"},"next":{"fields":{"title":"The Weekend Is Now","date":"16 Sep 2004","slug":"/2004-09-16-the-weekend-is-now/"}},"previous":{"fields":{"title":"Brown Nosing","date":"19 Sep 2004","slug":"/2004-09-19-brown-nosing/"}}}],"pathPrefix":"","first":false,"last":false,"index":419,"pageCount":1101}}

